  z-index: 2;
  left: 50%;
  display: inline-flex;
  list-style: none;
  transform: translateX(-50%);
}
@media (max-width: 767.98px) {
  .glide__bullets {
    bottom: 0.5rem;
  }
}
@media (min-width: 768px) and (max-width: 991.98px) {
  .glide__bullets {
    bottom: 1rem;
  }
}
@media (min-width: 992px) {
  .glide__bullets {
    bottom: 1.75rem;
  }
}
.glide__bullet {
  z-index: 3;
  opacity: 0.6;
  cursor: pointer;
  padding: 0;
  margin: 0;
  line-height: 0;
  transition: opacity 0.15s ease, background-color 0.2s, border 0.2s;
  background: var(--secondary-lightest);
  border: 1.5px solid var(--secondary-light);
  width: 1rem;
  height: 0.6rem;
  border-radius: 0.3rem;
  margin-left: 0.25rem;
}
.glide__bullet:first-child {
  margin-left: 0;
}
@media (min-width: 768px) {
  .glide__bullet {
    width: 1.6rem;
    margin-left: 0.3rem;
  }
  .glide__bullet:hover {
    border-color: var(--secondary);
    background-color: var(--secondary);
    opacity: 0.9;
  }
}
@media (min-width: 992px) {
  .glide__bullet {
    width: 2rem;
    height: 0.8rem;
    border-radius: 0.4rem;
    margin-left: 0.4rem;
  }
}
.glide__bullet:focus {
  outline: none;
}
.glide .glide__bullet--active {
  cursor: auto;
  border-color: var(--secondary);
  background-color: var(--secondary-lighter);
  opacity: 0.8;
}
.glide--swipeable {
  cursor: grab;
}
.glide--dragging {
  cursor: grabbing;
}

#overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -100;
  background: var(--dark);
  width: 100%;
  height: 100%;
}
#overlay.show {
  opacity: 0.4;
  cursor: pointer;
}